import React, { Component } from 'react'
import classes from './RoomBooking.module.css'
import { Link } from 'react-router-dom'
import Popups from '../../UI/Popups/Popups';
import firebaseApp from '../../firebase';
import Spinner from '../../UI/Spinner/spinner';

class RoomBooking extends Component{
    state={
        uploadingProcess:false,
        showBooking:false,
        bookingDet:{
            OwnerName:null,
            CustomerName:null,
            Rent:null,
            contactNo:null,
            imgUrl:null,
            bookingStatus:null,
            RoomAddress:null,
        }
    }
    componentDidMount(){
        const ownDet=this.props.history.location.state.val;
        
        this.setState(prevState=>{
            let bookingDet=Object.assign({},prevState.bookingDet)   
            bookingDet.OwnerName=ownDet.name 
            bookingDet.Rent=ownDet.price 
            bookingDet.imgUrl=ownDet.url 
            bookingDet.RoomAddress=ownDet.address
            return {bookingDet}
        })
    }
    onBooking=()=>{
        this.setState({uploadingProcess:true})
        console.log(this.state)
        const uplodOn=firebaseApp.database().ref('/BookingDet').push(this.state.bookingDet)
        uplodOn.then(res=>{
            console.log(res)
            this.setState({uploadingProcess:false,showBooking:true})
        }).catch(err=>console.log(err))   
    }
    handleCInput=event=>{
        const value = event.target.value;
        this.setState(prevState=>{
            let bookingDet=Object.assign({},prevState.bookingDet)   
            bookingDet.CustomerName=value
            return {bookingDet}
        })
    }

    handleCnInput=event=>{
        const value = event.target.value;
        this.setState(prevState=>{
            let bookingDet=Object.assign({},prevState.bookingDet)   
            bookingDet.contactNo=value
            return {bookingDet}
        })
    }

    render(){
        const Pval=this.props.history.location.state.val;
        let spinner=<Spinner/>
        let headerRe=<div className={classes.headerRe}>     
            <img  src={Pval.url}/>
        <p>Owner Name : {Pval.name}</p>
            <p>Rent Amount : {Pval.price}</p>
            <p>Room Location : {Pval.address}</p>
        </div>
        let contactInfo=<div className={classes.RoomBooking}>
        <Link className={classes.Link}  to="/"><i className="fa fa-home">Home</i></Link>
        <h1>Book Room</h1> 
        {headerRe}
         <p>Please provide the details for booking confirmation</p>
        <label>Enter Your Name </label>
        <input value={this.state.bookingDet.CustomerName} type="text" name="CustomerName"
                onChange={this.handleCInput} className={classes.input}/>
        <label>Mobile No</label>
        <input value={this.state.bookingDet.contactNo} name="contactNo" type="number"
                onChange={this.handleCnInput} className={classes.input}/>
        <div className={classes.buttonContainer}>
            <button className={classes.button} onClick={this.onBooking}>Book Room</button>
            <Link className={classes.button} to="/">Back</Link>
            </div>
    </div>   
  
        return(
            <div>
            <Popups show={this.state.showBooking}>
                <h2>Booked</h2>
                <button onClick={()=>this.setState({showBooking:false})}>Ok</button>
            </Popups>
            {this.state.uploadingProcess?spinner:contactInfo}
        </div>
    
        )
    }
}

export default RoomBooking;