import React from 'react'
import classes from './Popups.module.css'
const Popups=(props)=>{
    return(
        <div className={classes.Popups}
        style={{    
            transform:props.show?'translate(0)':'translate(2vh)',
            opacity:props.show?'1':'0'
        }}>
            {props.children}
        </div>)
}

export default Popups;