import React, { Component} from 'react'
import { Link } from 'react-router-dom';
import ShowDetail from '../ShowDetails/ShowDetails';
import classes from './Rooms.module.css'

class Rooms extends Component{
    
    render(){
        return(     
        <div className={classes.Rooms}>
       <img src={this.props.url}/>
       <div className={classes.labels}>
        <ShowDetail name="Rent" value={this.props.price} />
        <ShowDetail name="Owner Name" value={this.props.name} />
        <ShowDetail name="Address" value={this.props.address} />
        <Link  className={classes.Link} to={{pathname:'/BookRoom',state:{val:this.props}
    }} >Book Room</Link>
       </div>
        </div>
     )
    }
}
export default Rooms;   