import React from 'react';
import './App.css';
import Layout from './Layout/Layout';
import {BrowserRouter,Route, Link} from 'react-router-dom'
import RoomBooking from './Screens/RoomBooking/RoomBooking'
import SearchRoom from './Screens/SearchRoom/SearchRoom';
import RoomVideo from './Screens/RoomsVideo/RoomsVideo';
import AddRooms from './Screens/AddRooms/AddRooms';
import SignUp from './Screens/SignUp/Signup';
import BookigDetail from './Screens/BookingDetailsScreen/BookingDetail';

function App() {
  
  return (
    <div className="App">
    <BrowserRouter>
    <Layout />
    <Route path="/BookRoom" exact component={RoomBooking}/>
    <Route path="/SearchRoom" exact  component={SearchRoom} />
    <Route path="/" exact  component={SearchRoom} />
    <Route path="/SeeRoom" exact component={RoomVideo} />
    <Route path="/AddRoom" exact component={AddRooms} />
    <Route path="/SignUp" exact component={SignUp} />
    <Route path="/Booking" exact component={BookigDetail} />
    </BrowserRouter>
    </div>
    );
} 

export default App;
