import classes from './Signup.module.css';
import React from 'react'
import { Component } from 'react';
import firebaseApp from '../../firebase'
import Spinner from '../../UI/Spinner/spinner';
import { connect } from 'react-redux';

class SignUp extends Component {
    state = {
        userLoggedIn: false,spin:false,
        userType: true,
        loggedUser: null,valUser:false,
        error: null,SuccessMsg:null,
        semail: null, spassword: null,
        lemail: null, lpassword: null
    }
    toggleUserType = () => {
        const userL = this.state.userType;
        this.setState({ userType: !userL })
    }
    handleInput = event => {
        const name = event.target.name;
        const value = event.target.value;
        this.setState({ [name]: value })

    }
    //Logout
    logout = () => {
        this.setState({ userLoggedIn: false })
    }
    //Creating New User In DataBase
    logSubmit = () => {
        this.setState({error:null,spin:true})
        const nauth = firebaseApp.auth().createUserWithEmailAndPassword(this.state.lemail, this.state.lpassword)
        nauth.then(res => {
                console.log(res);
                this.setState({SuccessMsg:res.user.email+'  '+'User Created'})

                this.setState({spin:false})
        }).catch(err => {
            //console.log(err);
            if(err.code='auth/email-already-in-use'){
                this.setState({error:'Email is Already Registered'})
            }
            else if(err.code=='auth/argument-error'){
                this.setState({error:'Password is not valid'})
            }
            else if(err.code=='auth/invalid-email') {
                this.setState({error:'Invalid Email'})
            }
            else if(err.code=='auth/weak-password'){
                this.setState({error:'Password is too weak'})
            }
            else if(err.code=='auth/network-request-failed'){
                this.setState({error:'No Internet'})
            }
            this.setState({spin:false})
            })
    }
    //For Validationg User

    valUser=()=>{
        if(this.state.semail||this.state.lemail==''){
            this.setState({valUser:false})
        }
        else{
            this.setState({valUser:true})    
        }
    }
    //On Login Submit
    onSubmit = () => {
        this.setState({spin:true,error:null})
        console.log(this.state.spassword)
        const auths = firebaseApp.auth().signInWithEmailAndPassword(this.state.semail, this.state.spassword);
        auths.then(res => {
            this.setState({ loggedUser: res.user.email, userLoggedIn: true })
           this.setState({spin:false})
           this.props.chUser()
            this.props.history.push('/SearchRoom',{name:res.user.email,lon:''})

           }).catch(err => {
            console.log(err.code)
            if (err.code=='auth/wrong-password') {
                this.setState({error:'Password is Wrong'})
            }
            if (err.code=='auth/user-not-found') {
                this.setState({error:'User Not Found'})
            }
            if (err.code=='auth/too-many-requests'){
                this.setState({error:'You have reached maximum try'})
            }
            else if(err.code=='auth/network-request-failed'){
                this.setState({error:'No Internet'})
            }
            this.setState({spin:false})
        })
    }
    
    render() {
        let login =<div>
            <div className={classes.SignUp}>
                <form className={classes.forml}>
                    <input type="text"
                        onChange={this.handleInput}
                        name="lemail"
                        value={this.state.lemail}
                        placeholder="Email Address" />
                    <input type="password" name="lpassword" onChange={this.handleInput} value={this.state.lpassword} placeholder="Password" />
                </form>
                <button onClick={this.logSubmit}>SignUp</button>

            </div>
            <div>
                <button onClick={this.toggleUserType} className={classes.already}>Already a Member ?</button>
            </div>
        </div>
        let signup = <div>
            <div className={classes.SignUp}>
                <form className={classes.forml}>
                    <input type="text"
                    onChange={this.handleInput}
                    name="semail"
                    value={this.state.semail}
                    placeholder="Email Address" />
                    <input type="password" name="spassword" onChange={this.handleInput} value={this.state.spassword} placeholder="Password" />
                </form>
                <button onClick={this.onSubmit}>Login</button>
            </div>
            <div>
                <button onClick={this.toggleUserType} className={classes.already}>Create Account</button>
            </div>
        </div>
        let loggedUser = <div>
            <h3>User Logged in Successsfullly User Name :{this.state.loggedUser}</h3>
            <button onClick={this.props.chUser}>Log out</button>
        </div>
        let spinner=<Spinner/>
        
        let renelement;
        if (this.props.userLog) {
            renelement = loggedUser
        }
        else if (this.state.userType) {
            renelement = signup
        }
        else {
            renelement = login
        }
        return (
            <div>
                {this.state.error}
                <div className={classes.succM}>
                {this.state.SuccessMsg}
                </div>
                {this.state.spin?spinner:renelement}
            </div>
        )
    }
}

const mapStateToProps=state=>{
    return {
        userLog:state.userLogged
    }
}

const mapdispatchToProps=dispatch=>{
    return {
        chUser:()=>dispatch({type:'CH_USER_TP'})
    }
}
export default connect(mapStateToProps,mapdispatchToProps)(SignUp);

