import React, { Component } from 'react'
import classes from './BookingDetail.module.css' 
import firebaseApp from '../../firebase'
class BookigDetail extends Component{
    state={
        RoomDetail:[]
    }
    componentDidMount(){
        const dataRef = firebaseApp.database().ref('BookingDet');
        dataRef.on('value', snapshot => {
            let newRoomsDetail = [];
            snapshot.forEach(data => {
                const dataVal = data.val()
                newRoomsDetail.push({
                    id: data.key,
                    CustomerName: dataVal.CustomerName,
                    OwnerName: dataVal.OwnerName,
                    rent: dataVal.Rent, 
                    address: dataVal.RoomAddress,
                    contact: dataVal.contactNo,
                    url: dataVal.imgUrl,
                    })
            })
            this.setState({ RoomDetail: newRoomsDetail })
            
        })
    
    }
    render(){

        let bookingHeader=<div className={classes.bookingC}>
            <h4>Customer Name</h4>
            <h4>Contact No</h4>
            <h4>Owner Name</h4>
            <h4>Rent Amount</h4>
            <h4>Room Address</h4>
            
        </div>
        let bookingDet = <div className={classes.booking}>
            {
                this.state.RoomDetail.map(data => {
                    return (
                        <div className={classes.bookingC}>
                        <p>{data.CustomerName}</p>
                        <p>{data.contact}</p>
                            <p>{data.OwnerName}</p>
                            <p>{data.rent}</p>
                            <p>{data.address}</p>
                        </div>
                    )
                })}
        </div>
        return (
            <div>
                {bookingHeader}
                {bookingDet}
            </div>
        )
    }
}


export default BookigDetail