import React, { Component } from 'react'
import classes from './AddRooms.module.css'
import { Link } from 'react-router-dom'
import firebaseApp from '../../firebase';
import Spinner from '../../UI/Spinner/spinner';


class AddRooms extends Component {
    state = {
        uploadProcessing: false,
        active: false,
        image: null,
        Price: null,
        OwnerName: null,
        Address: null,
        url: null
    }
    componentDidMount() {
        console.log(this.props.history)
    }
    fileSelectHandler = event => {
        this.setState({ image: event.target.files[0] })
    }
    handleInput = event => {
        const name = event.target.name;
        const value = event.target.value;
        this.setState({ [name]: value })
        if (this.state.OwnerName != null && this.state.Price != null && this.state.Address != null) {
            this.setState({ active: true })
        }
    }

    onSubmitHandler = () => {
        this.setState({ uploadProcessing: true })
        const uploadTask = firebaseApp.storage().ref(`RoomImages/${this.state.image.name}`).put(this.state.image)
        uploadTask.on("state_changed", snapshot => { }, error => {
            console.log(error)
        },
            () => {
                firebaseApp.storage().ref("RoomImages").child(this.state.image.name)
                    .getDownloadURL()
                    .then(url => {
                        this.setState({ url })
                        firebaseApp.database().ref('/Rooms').push(this.state);
                        this.props.history.push('/SearchRoom')
                        this.setState({ uploadProcessing: false })
                    })
            }
        )
    }
    render() {
        let AddRoom = <div>
            <input onChange={this.handleInput}
                name="Price"
                value={this.state.Price}
                className={classes.input} type="text"
                placeholder="enter rent amount in rupees" />
            <input onChange={this.handleInput}
                name="OwnerName" value={this.state.OwnerName}
                className={classes.input} type="text" placeholder="enter Name of Owner" />
            <input
                name="Address"
                value={this.state.Address}
                onChange={this.handleInput} className={classes.input} type="text" placeholder="enter Address where Room is located" />
            <br />
            <label>Upload Image</label><br />
            <input type="file" onChange={this.fileSelectHandler} /><br />
            <button disabled={!this.state.active} className={classes.button} onClick={this.onSubmitHandler}>Upload</button>
        </div>
        let spinner = <Spinner />

        return (<div className={classes.AddRooms}>
            <Link className={classes.Link} to="/"><i className="fa fa-home">Home</i></Link>
            <h1>Add Rooms</h1>
            <h3>Enter all the details</h3>
            {this.state.uploadProcessing ? spinner : AddRoom}
        </div>
        )
    }
}
export default AddRooms;