import React, { Component } from 'react'
import classes from './SearchRoom.module.css'
import Rooms from '../../Containers/Rooms/Rooms'
import firebaseApp from '../../firebase'
import Spinner from '../../UI/Spinner/spinner';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
class SearchRoom extends Component {
    state = {
        rooms: [],
        loading:true
    }
    componentDidMount() {
        console.log(this.props.history)
        const dataRef = firebaseApp.database().ref('Rooms');
        dataRef.on('value', snapshot => {
            let newRooms = [];
            snapshot.forEach(data => {
                const dataVal = data.val()
                newRooms.push({
                    id: data.key,
                    name: dataVal.OwnerName,
                    address: dataVal.Address,
                    price: dataVal.Price,
                    url: dataVal.url
                })
            })
            this.setState({ rooms: newRooms,loading:false })
            
        })
    }
    render() {
        let SearchRoom=<div>
                {
                    this.state.rooms.map(datal => {
                        return <Rooms name={datal.name} address={datal.address} price={datal.price} url={datal.url} />
                    })
                }
        </div>
        let spinner=<Spinner/>
        let logi=<div><Link to='/SignUp'>Owner Login</Link></div>
        let welcomeUser;
                if(this.props.logUser){
                    welcomeUser=<div><h3>Welcome : {}</h3>
            <button onClick={this.props.chUser}>Log out</button>
                    </div>
                }
       return (
            <div className={classes.SearchRoom}><br />
            {this.props.logUser?welcomeUser:logi}
 
                <input className={classes.input} placeholder="SearchRooms..." />
            <h1>Search Rooms</h1>
            {
            this.state.loading?spinner:SearchRoom
            }
            </div>
        )
    }
}

const mapStateToProps=state=>{
return{
    logUser:state.userLogged
}
}
const mapdispatchToProps=dispatch=>{
    return {
        chUser:()=>dispatch({type:'CH_USER_TP'})
    }
}

export default connect(mapStateToProps,mapdispatchToProps)(SearchRoom);